import { Box } from '@mui/system';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import Container from '@mui/material/Container';
import DialogTitle from '@mui/material/DialogTitle';
import DialogActions from '@mui/material/DialogActions';
import { IconButton, Typography, DialogContent } from '@mui/material';

import { paths } from 'src/routes/paths';

import { useBoolean } from 'src/hooks/use-boolean';

import { useTranslate } from 'src/locales';

import CustomBreadcrumbs from 'src/components/custom-breadcrumbs';

// ----------------------------------------------------------------------

import { RouterLink } from 'src/routes/components';

import Iconify from 'src/components/iconify';

import Familymem from '../familymem';

export default function Family() {
  const dialog = useBoolean(false);

  const { t } = useTranslate();
  return (
    <Container maxWidth="xl">
      <CustomBreadcrumbs
        heading={t('my family')}
        links={[
          { name: t('dashboard'), href: paths.dashboard.root },
          { name: t('user'), href: paths.dashboard.user.root },
          { name: t('my family') },
        ]}
        action={
          <Button
            component={RouterLink}
            onClick={dialog.onTrue}
            variant="contained"
            startIcon={<Iconify icon="mingcute:add-line" />}
          >
            {t('Add new')}
          </Button>
        }
        sx={{ mb: { xs: 3, md: 5 } }}
      />

      <Dialog open={dialog.value} onClose={dialog.onTrue}>
        <IconButton onClick={dialog.onFalse} sx={{ ml: 'auto', mt: 2 }}>
          <Iconify width={25} icon="iconoir:cancel" />
        </IconButton>
        <DialogContent>
          <ul>
            <li style={{ opacity: '50%' }}>
              {t('Search or add a user on the Hakimna platform to become a family member')}
            </li>
          </ul>
        </DialogContent>
        <DialogTitle>
          <Typography variant="h4">{t('Does the user have an account?')}</Typography>
        </DialogTitle>

        <DialogActions>
          <Button
            href={paths.dashboard.user.exist}
            variant="outlined"
            color="success"
            type="submit"
          >
            {t('Yes')}
          </Button>
          <Button
            type="submit"
            variant="contained"
            color="inherit"
            href={paths.dashboard.user.create}
          >
            {t('No')}
          </Button>
        </DialogActions>
      </Dialog>

      <Box sx={{ display: 'grid', gridTemplateColumns: '1fr 1fr 1fr', gap: 3 }}>
        <Familymem />
      </Box>
    </Container>
  );
}
